<template>
	<div>
		<header-nav v-bind:class="{'isShare':active}" class="dateInfo_head"></header-nav>
		<div class="main">
			<h1>第一篇：使用vue-cli+webpack搭建SPA项目</h1><br />
			<p>使用vue官方推荐的方式构建项目，默认装过node啦</p>
			<p><span>1.</span>打开cmd命令行工具，输入npm install -g vue-cli,回车 全局安装vue-cli</p><br />
			<p>cmd中切换到项目目录下 输入：npm install webpack projectname （注：projectname为你自己想创建的文件名称）</p>
			<p>回车之后，会出现下图所示</p>
			<p><img src="static/images/creatObj.png"/></p>
			<p>Project name:——项目名称</p>
			<p>Project description:——项目描述</p>
			<p>Author:——作者</p>
			<p>Vue build:——构建模式，一般默认选择第一种</p>
			<p>Install vue-router?:——是否安装引入vue-router，这里选是，vue-router是路由组件,后面构建项目会用到</p>
			<p>Use ESLint to lint your code?:——这里强烈建议选no 否则你会非常痛苦，eslint的格式验证非常严格，多一个空格少一个空格都会报错，所以对于新手来说，一般不建议开启，会加大开发难度</p>
			<p>Setup unit tests with Karma + Mocha 以及Setup e2e tests with Nightwatch这两个是测试，可以不用安装</p>	
			<p><span>2.</span>安装相关的依赖</p><br />
			<p>在cmd中输入:node install ;回车安装相关依赖；node 中安装依赖输入：node install 依赖名   --save-dev  即可</p>
			<p>安装成功之后会生成node_modules文件夹</p>
			<p><span>3.</span>运行项目</p><br />
			<p>在cmd中输入：npm run dev 就可以启动项目，会自动大开http://localhost：8080</p>
			<p><span>注意：</span>端口80被占用的情况，修改端口号方法：打开config文件夹下边的index.js文件</p>
			<p>在index.js中的port改成你想要的端口号，运行即可</p>
		</div>
	</div>
	
</template>

<script>
	import HeaderNav from '../../components/header.vue'
	
	export default {
	  data () {
	    return {
	      active:true
	    }
	  },
	  components:{
	  	HeaderNav
	  },
	  methods:{
	  }
	}
</script>

<style scoped>
	@import '../../common/css/common.css';
	
</style>


